
.flex-test-container {
  width: 500px;
  height: 100px;
  border: 1px solid #ddd;
  display: flex;
  margin-bottom: 30px;
}
.items {
  margin: 10px;
  width: 30px;
  height: 20px;
  background: blue;
}
.c4 {
  flex-wrap: wrap;
}
.i1 {
  flex: 0 1 auto;
}
.i2 {
  flex: 0 0 auto;
}
.i3 {
  flex: 1 1 auto;
}
.i4 {
  flex-shrink: 0;
}



